import template from './template/Drawer.html'
import { RAW_STRING, RAW_BOOLEAN, TRUE, FALSE } from '../constant'

export default {
  propTypes: {
    title: {
      type: RAW_STRING
    },
    width: {
      type: RAW_STRING,
      value: 256
    },
    height: {
      type: RAW_STRING,
      value: 256
    },
    maskClosable: {
      type: RAW_BOOLEAN,
      value: TRUE
    },
    closable: {
      type: RAW_BOOLEAN,
      value: FALSE
    },
    placement: {
      type: RAW_STRING,
      value: 'left'
    },
    mask: {
      type: RAW_BOOLEAN,
      value: TRUE
    },
    open: {
      type: RAW_BOOLEAN
    },
    className: {
      type: RAW_STRING
    },
    style: {
      type: RAW_STRING
    }
  },

  model: 'open',

  template,

  watchers: {
    open(isOpen) {
      let element = this.$el
      if (isOpen) {
        Yox.dom.addClass(element, 'bell-drawer-open')
        this.fire('open.drawer')
      }
      else {
        Yox.dom.addClass(element, 'bell-drawer-leave')
        setTimeout(
          function () {
            Yox.dom.removeClass(element, 'bell-drawer-open')
            Yox.dom.removeClass(element, 'bell-drawer-leave')
          },
          300
        )
        this.fire('close.drawer')
      }
    }
  },

  computed: {
    contentStyle() {
      let style = ''
      if (this.get('placement') === 'left'
        || this.get('placement') === 'right'
      ) {
        let width = +this.get('width')
        if (width < 100) {
          style = `width: ${width}%;`
        }
        else {
          style = `width: ${width}px;`
        }
      }
      else {
        let height = +this.get('height')
        if (height < 100) {
          style = `height: ${height}%;`
        }
        else {
          style = `height: ${height}px;`
        }
      }
      return style
    }
  },

  afterMount() {
    Yox.dom.append(document.body, this.$el)
  },

  beforeDestroy() {
    Yox.dom.remove(document.body, this.$el)
  }
}